import React, { Component } from 'react';
import TabBar from 	'antd-mobile/lib/tab-bar'
import Icon from 	'antd-mobile/lib/icon'
import NavBarindex from './header.js'

class TabBarCommon extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'blueTab',
      hidden: false,
    };
  }

  renderContent(pageText) {
    return (
      <div style={{ backgroundColor: 'white', height: '100%', textAlign: 'center' }}>
        <div>{pageText}</div>
      </div>
    );
  }

  render() {
    return (
      <TabBar
        unselectedTintColor="#949494"
        tintColor="#ed6460"
        barTintColor="white"
        hidden={this.state.hidden}
      >
        <TabBar.Item
          title=""
          key=""
          icon={<div style={{
            width: '0.44rem',
            height: '0.44rem',
            background: 'url(http://ovj4n4xu6.bkt.clouddn.com/notebook.svg) center center /  0.42rem 0.42rem no-repeat' }}
          />
          }
          selectedIcon={<div style={{
            width: '0.44rem',
            height: '0.44rem',
            background: 'url(http://ovj4n4xu6.bkt.clouddn.com/notebook.svg) center center /  0.42rem 0.42rem no-repeat' }}
          />
          }
          selected={this.state.selectedTab === 'blueTab'}
          onPress={() => {
            this.setState({
              selectedTab: 'blueTab',
            });
          }}
          data-seed="logId"
        >
          {this.renderContent(<NavBarindex/>)}
        </TabBar.Item>
        <TabBar.Item
		icon={<div style={{
            width: '0.44rem',
            height: '0.44rem',
            background: 'url(http://ovj4n4xu6.bkt.clouddn.com/telephone.svg) center center /  0.42rem 0.42rem no-repeat' }}
          />
          }
          selectedIcon={<div style={{
            width: '0.44rem',
            height: '0.44rem',
            background: 'url(http://ovj4n4xu6.bkt.clouddn.com/telephone.svg) center center /  0.42rem 0.42rem no-repeat' }}
          />
          }
          title=""
          key=""
          selected={this.state.selectedTab === 'redTab'}
          onPress={() => {
            this.setState({
              selectedTab: 'redTab',
            });
          }}
          data-seed="logId1"
        >
          {this.renderContent('')}
        </TabBar.Item>
        <TabBar.Item
          icon={
            <div style={{
              width: '0.44rem',
              height: '0.44rem',
              background: 'url(http://ovj4n4xu6.bkt.clouddn.com/cup%20%281%29.svg) center center /  0.42rem 0.42rem no-repeat' }}
            />
          }
          selectedIcon={
            <div style={{
              width: '0.44rem',
              height: '0.44rem',
              background: 'url(http://ovj4n4xu6.bkt.clouddn.com/cup%20%281%29.svg) center center /  0.42rem 0.42rem no-repeat' }}
            />
          }
          title=""
          key=""
          selected={this.state.selectedTab === 'greenTab'}
          onPress={() => {
            this.setState({
              selectedTab: 'greenTab',
            });
          }}
        >
          {this.renderContent('')}
        </TabBar.Item>
      </TabBar>
    );
  }
}

export default TabBarCommon


