import React, { Component } from 'react';
import Tabs from 'antd-mobile/lib/tabs'
import Recommend from './recommend/recommendmale.js'
const TabPane = Tabs.TabPane;

function callback(key) {
  console.log('onChange', key);
}
function handleTabClick(key) {
  console.log('onTabClick', key);
}
class Tabsecond extends React.Component {
  constructor(props) {
   super(props)
  }
  render(){
    return   <div>
    <Tabs swipeable={false} className='manyTabs' defaultActiveKey="1" onChange={callback} onTabClick={handleTabClick}>
      <TabPane tab='' key="1" style={{flexGrow:1}}>
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', backgroundColor: '#fff' }}>
          <Recommend/>
        </div>
      </TabPane>
      <TabPane tab='' key="2" style={{flexGrow:1}}>
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '5rem', backgroundColor: '#fff' }}>
          Content of Second Tab
        </div>
      </TabPane>
      <TabPane tab='' key="3"  style={{flexGrow:1}}>
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '5rem', backgroundColor: '#fff' }}>
          Content of Third Tab
        </div>
      </TabPane>
      <TabPane tab='' key="4"  style={{flexGrow:1}}>
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '5rem', backgroundColor: '#fff' }}>
          Content of Third Tab
        </div>
      </TabPane>
      <TabPane tab='' key="5"  style={{flexGrow:1}}>
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '5rem', backgroundColor: '#fff' }}>
          Content of Third Tab
        </div>
      </TabPane>
    </Tabs>
  </div>

  }

}
export default Tabsecond


